@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin BigScreen {
  @media (min-width:2000px) {
    @content;
  }
}

@mixin Desktop {
  @media (max-width: 1280px){
      @content;
  }
}

@mixin Tablet {
  @media (max-width: 768px){
      @content;
  }
}

@mixin Mobile {
  @media (max-width: 500px){
      @content;
  }
}
