@import "../utils";

.Language {
    display: none;
    background: $primary_color;
    flex-direction: column;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 3;
    transform: translateX(-100%);
    height: 100%;
    background: $bg-color;

    @include Tablet {
        display: flex;
    }

    @include Mobile {
        display: flex;
    }

    &:target {
        transform: translateX(0);
    }

    &__close-con {
        display: flex;
        padding-top: 34px;
        padding-bottom: 46px;

        @include Mobile {
            padding-top: 20px;
            padding-bottom: 37px;
        }
    }

    &__close {
        display: block;
        cursor: pointer;
        margin-left: 34px;
        width: 17px;
        height: 17px;

        &:hover {
            //blue color filer later
        }

        @include Mobile {
            margin-left: 20px;
        }
    }

    &__links-con{
        display: flex;
        flex-direction: column;
    }

    &__link{
        font-family: $font-family;
        cursor: pointer;
        font-weight: 500;
        font-size: 21px;
        color: $primary-color;
        padding-top: 11px;
        padding-bottom: 11px;
        width: fit-content;
        padding-left: 153px;

        &:hover{
            width: 100%;
            color: $secondary-color;
            background: $menu-hover;
        }

        @include Mobile{
            padding-left: 22px;
        }
    }
}